






































































































































.pie {
    display: inline-block;
    overflow: hidden;
    //width: 110px;
    margin: 0 auto;

    &-wrapper {
        position: relative;
        z-index: 0;
        left: 0;
        top: 0;
        width: 36px;
        height: 36px;
        line-height: 36px;
        border-radius: 50%;
        text-align: center;
        color: #000;
        font-size: 36%;
        background-color: #f0f0f0;
        background-image: linear-gradient(to right, transparent 50%, yellowgreen 0);
        cursor: pointer;
        overflow: hidden;
        margin: 0 auto;

        strong {
            position: absolute;
            width: 100%;
            left: 0;
            z-index: 1;
            margin: 0;
            padding: 0;
        }

        span {
            display: block;
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
            margin: 0;
            padding: 0;
        }

        div {
            position: absolute;
            top: 0;
            width: 60%;
            height: 100%;
            left: 50%;
            transform: rotate(0turn);
            transform-origin: center left;
            transition: transform 0.7s linear;
            margin: 0;
            padding: 0;
        }

        > .div_1 {
            background-color: inherit;
            z-index: -2;
        }

        > .div_2 {
            opacity: 0;
            z-index: -1;
            background-color: yellowgreen;
        }
    }

    &-left {
        float: left;
        //width: 40%;
    }

    &-right {
        float: right;
        //width: 60%;
        margin-top: 10px;
        margin-left: 10px;
        text-align: right;
    }
}
